
#app {
  padding:10px 0;
  margin:0 auto;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width:100%;
  height:100%;
}
/* 重置 */
html,body,div,p,ul,ol,li,h1,h2,h3,h4,h5,h6{
  margin:0;
  padding:0;
}
html,body{
  width: 100%;
  height: 100%;
}
body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background: #f5f5f5;
}
input {
  outline: none;
}
ul,li,ol {
  list-style: none;
}
a {
  text-decoration: none;
  color: rgb(145, 145, 145);
}
div{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
/* 默认 */
strong,em{font-style:normal;font-weight:bold;
}

#main{
  padding:10px 0;
  margin:0 auto;
  width:600px;
  height:100%;
  text-align: center;
  /* border: 1px black solid; */
}
#main .g-header{
  width: 100%;
  color: rgba(175,47,47,.15);
  text-rendering: optimizeLegibility; /*浏览器强调了渲染速度和几何精度的可读性。这样可以进行字距调整和可选的连字。*/
  /* border: 2px green solid; */
}
#main .g-header h1{
  font-size: 100px;
  font-weight: 100;
}
#main .g-header .f-add{
  position: relative;
  margin: 10px 0 0;
  /* border: 1px red solid; */
}
#main .g-header .f-add .switch{
  width: 26px;
  height: 26px;
  background:url(data:image/png;base64,) center center no-repeat;
  position: absolute;
  top: 18px;
  left: 22px;
  cursor: pointer;
}
#main .g-header .f-add .f-add-task{
  padding: 16px 16px 16px 60px;
  border: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: inset 0 -2px 1px rgba(222, 222, 222, 0.03);
          box-shadow: inset 0 -2px 1px rgba(222, 222, 222, 0.03);
  width: 100%;
  font-size: 24px;
  color: #000;
  font-weight: inherit;
}
#main .g-header .f-add .f-add-button{
  position: absolute;
  width: 80px;
  height: 60px;
  line-height: 20px;
  top: 0px;
  right: 0px;
  cursor: pointer;
}

/* content */
#main .g-content{
  position: relative;
  margin: 0 auto;
  width: 100%;
  -webkit-box-shadow: 0 3px 3px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0 3px 3px 2px rgba(0, 0, 0, 0.25);
}
#main .g-content .g-tasklist{
  position: relative;
  z-index: 6;
}
#main .g-content .g-tasklist .m-tasks{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  font-size:24px;
  padding:8px;
  background: #fff;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  /* border: 1px red solid; */
}
#main .g-content .g-tasklist .m-tasks:hover{
  background-color: #ccc;
}
#main .g-content .g-tasklist .m-tasks .u-check{
  width: 24px;
  height: 24px;
  margin-left: 12px;
  /* border: 1px red solid; */
}
.u-close-icon{
  background:url(data:image/png;base64,) no-repeat;
  background-size: 100%;
}
#main .g-content .g-tasklist .m-tasks:hover .u-close{
  position: absolute;
  right: 1rem;
  cursor: pointer;
  width: 1.5rem;
  height: 1.5rem;
  display: block;
}
.u-message{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  text-align: left;
  margin-left: 16px;
  font-size: 20px;
  padding: 6px 0;
  word-break: break-all;
  display: block;
  line-height: 1.2;
  -webkit-transition: color 0.4s;
  transition: color 0.4s;
}
.u-update{
  position: relative;
  z-index:1;
}
.g-footer-btn{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: .1rem;
  font-size: 16px;
  font-weight: 300;
  color: rgb(145, 145, 145);
}
.g-footer-btn .g-data-num{
  margin-left: 10px;
  /* background-color: red; */
}
.g-footer-btn .g-filter li{
  /* border: 1px red solid; */
  float: left;
  margin: auto 10px;
  line-height: 100%;
}
.g-footer-btn .g-filter li a:hover {
  background-color: aqua;
  border-color: rgba(34, 26, 26, 0.1);
}
.g-footer-btn .g-filter li a.selected {
  border-color: rgba(240, 10, 10, 0);
}
.f_delete{
  text-decoration: line-through;
  color: #bbb;
}
.clear-completed{
  cursor: pointer;
}
